.search-page__wrapper {
    display: grid;
    grid-column: span 17;
    grid-template-columns: repeat(12, 1fr);
    grid-column-gap: 50px;
    .breadcrumb__wrapper {
        grid-column: span 12;
    }
    .search-rs__wrapper{
        grid-column: span 12;
        border: 4px dashed blue;
        .search-rs__content{
            display: grid;
            grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
            grid-gap: 30px;
            .post-item__wrapper{
                grid-column: span 1;
            }
        }
    }
}
