* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
html,
body {
    position: relative;
    &.dark-theme{
        @each $name, $color in $theme-dark {
            --#{$name}: #{$color};
        }
    }
    &.light-theme{
        @each $name, $color in $theme-light {
            --#{$name}: #{$color};
        }
    }
    font-family: "Roboto", sans-serif;
    font-size: 14px;
    color: var(--cl_txt_02);
    line-height: $line-height-0;
    background-color: var(--cl_bg_01);
    .container {
        width: 100%;
        max-width: 1700px;
        margin: 0 auto;
    }
}
ul{
    list-style: none;
}
a{
    text-decoration: none;
    width: 100%;
    height: 100%;
    color: inherit;
}
hr{
    margin-top: 30px;
    border-top: 5px dashed #ccc;
}