@import url("https://fonts.googleapis.com/css2?family=Roboto:ital,wght@0,100;0,300;0,400;0,500;0,700;0,900;1,100;1,300;1,400;1,500;1,700;1,900&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Lora:wght@600&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Gotu&display=swap");

//font-family;
$base-font-family: "Roboto", "Helvetica Neue", Helvetica, Arial, sans-serif;
$alt-font-family: "Gotu", Georgia, sans-serif;
$branch-font-family: "";
// font size
$txt-base-size: .8rem; // body font-size
$txt-scale-ratio: 1.2; // multiplier used to generate the scale values üëá

// type scale
$txt-xs: ($txt-base-size / $txt-scale-ratio / $txt-scale-ratio);
$txt-sm: ($txt-xs * $txt-scale-ratio);
$txt-md: ($txt-sm * $txt-scale-ratio * $txt-scale-ratio);
$txt-lg: ($txt-md * $txt-scale-ratio);
$txt-xl: ($txt-lg * $txt-scale-ratio);
$txt-xxl: ($txt-xl * $txt-scale-ratio);
$txt-xxxl: ($txt-xxl * $txt-scale-ratio);
$txt-xxxxl: ($txt-xxxl * $txt-scale-ratio);

//lineheight
$heading-line-height: 1.2;
$body-line-height: 1.58;
// $line-height-multiplier: 1; // modify line-height of all txt elements
// $txt-vspace-multiplier: 6; // modify vertical spacing

// screen scale;
// @include breakpoint(extra-large) {
//     :root {
//         $txt-base-size: 1.25em;
//         $txt-scale-ratio: 1.25;
//     }
// }
@include breakpoint(large) {
    $txt-base-size: 1.25em;
    $txt-scale-ratio: 1.25;
}
@include breakpoint(medium) {
    $txt-base-size: 1.25em;
    $txt-scale-ratio: 1.25;
}
@include breakpoint(small) {
    $txt-base-size: 1.25em;
    $txt-scale-ratio: 1.25;
}
